<template>
  <div class="timing_list mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="roleName"
          placeholder=""
          @keyup.enter.native="getrole(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getrole(1)"></el-button>
        <el-button type="primary" @click="adddata"></el-button>
      </div>
    </div>
    <el-table
      :data="userlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
    >
      <el-table-column prop="username" align="center" sortable label="job id"></el-table-column>
      <el-table-column prop="trueName" align="center" sortable label=""></el-table-column>
      <el-table-column prop="username" align="center" sortable label=""></el-table-column>
      <el-table-column prop="username" align="center" sortable label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>

          <el-button
            @click="handledel(scope.row)"
            type="danger"
            size="mini"
            icon="el-icon-delete"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <Pagein
      :total="total"
      @handleCurrentChange="handleCurrentChange"
      @handleSizeChange="handleSizeChange"
    ></Pagein>

    <el-dialog
      title=""
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="30%"
      min-width="30%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="job id" prop="job">
          <el-input v-model="ruleForm.job" clearable placeholder="ID"></el-input>
        </el-form-item>
        <el-form-item label="" prop="order">
          <el-input v-model="ruleForm.order" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="timer">
          <el-input
            v-model="ruleForm.timer"
            clearable
            placeholder="linux crontab     "
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import Pagein from "../../public/pagein";
export default {
  name: "timing_list",
  data() {
    return {
      roleName: "",
      total: 100,
      loading: false,
      DialogVisible: false,
      ruleForm: {
        job: "",
        order: "",
        timer: ""
      },
      rules: {
        job: [{ required: true, message: "job id", trigger: "blur" }],
        order: [
          { required: true, message: "", trigger: "blur" }
        ],
        timer: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      userlist: []
    };
  },
  components: {
    Pagein
  },

  methods: {
    //
    adddata() {
      this.DialogVisible = true;
    },
    //
    handleCurrentChange(val) {
      console.log("handleCurrentChange", val);
    },
    //
    handleSizeChange(val) {
      console.log("handleSizeChange", val);
    },
    handleedit(obj) {},
    handledel(obj) {},
    //
    changestatus(obj) {},
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.DialogVisible = false;
          this.$message.success("");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="less" scoped>
.el-input {
  width: 50%;
}
</style>




