<template>
  <div class="user_list mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.name"
          placeholder=""
          @keyup.enter.native="getuserlist(1)"
          clearable
        ></el-input>
        <el-input
          v-model="term.trueName"
          placeholder=""
          @keyup.enter.native="getuserlist(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getuserlist(1)"></el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)"></el-button>
      </div>
    </div>

    <el-table
      :data="userlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
    >
      <el-table-column prop="username" align="center" label=""></el-table-column>
      <el-table-column prop="trueName" align="center" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">{{scope.row.cityId | fitercityId}}</template>
      </el-table-column>
      <el-table-column prop="createTime" align="center" label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" align="center" width="350px">
        <template slot-scope="scope">
          <el-button
            :disabled="!scope.row.status"
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            :disabled="!scope.row.status"
            @click="handledeploy(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-service"
          ></el-button>
          <el-button
            :disabled="!scope.row.status"
            @click="handlereset(scope.row)"
            type="danger"
            size="mini"
            icon="el-icon-setting"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
    <!-- 84 -->
    <el-dialog
      v-if="AddDialogVisible"
      title=""
      :visible.sync="AddDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        v-show="disval==1 || disval==2"
      >
        <el-form-item label="">
          <el-input v-model="ruleForm.username" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="ruleForm.mobile" maxlength="11" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="ruleForm.password" maxlength="255" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="ruleForm.code" placeholder="">
            <el-button slot="append" @click="handlecode" :disabled="num>0?true:false">
              
              <i v-show="num">({{num}}s)</i>
            </el-button>
          </el-input>
          <!-- <el-input v-model="ruleForm.code" maxlength="255" clearable></!-->
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
var resultsCode = "";
const checkPhone = (rule, value, callback) => {
  const mailReg = /^1[3456789]\d{9}$/;
  if (!value) {
    return callback(new Error(""));
  }
  setTimeout(() => {
    if (mailReg.test(value)) {
      callback();
    } else {
      callback(new Error(""));
    }
  }, 100);
};

const checkCode = (rule, value, callback) => {
  const mailReg = /^1[3456789]\d{9}$/;
  if (!value) {
    return callback(new Error(""));
  }
  setTimeout(() => {
    if (value == resultsCode) {
      callback();
    } else {
      callback(new Error(""));
    }
  }, 100);
};

export default {
  name: "perlist",
  data() {
    return {
      timer: null,
      num: 0,
      total: 0,
      disval: 1,
      pageNum: 1,
      pageSize: 10,
      term: {
        trueName: "",
        name: ""
      },

      loading: false,
      AddDialogVisible: false,
      actuserobj: {},
      ruleForm: {
        username: "",
        password: "",
        mobile: "",
        code: ""
      },
      rules: {
        mobile: [{ required: true, validator: checkPhone, trigger: "blur" }],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^(?![a-zA-Z]+$)(?![A-Z0-9]+$)(?![A-Z\W_]+$)(?![a-z0-9]+$)(?![a-z\W_]+$)(?![0-9\W_]+$)[a-zA-Z0-9\W_]{8,30}$/,
            message:
              "  8 - 30"
          }
        ],
        code: [{ required: true, validator: checkCode, trigger: "blur" }]
      },
      userlist: []
    };
  },
  filters: {},
  watch: {
    AddDialogVisible: function() {
      if (!this.AddDialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
        this.actuserobj = {};
      }
    },
    "ruleForm.mobile": {
      handler: function() {
        clearInterval(this.timer);
        this.num = 0;
      }
    }
  },
  methods: {
    //
    handlecode() {
      const reg = /^1[3|4|5|6|7|8][0-9]\d{8}$/;
      if (this.ruleForm.mobile) {
        if (reg.test(this.ruleForm.mobile)) {
          this.$http
            .post("mobiles/", {
              mobile: this.ruleForm.mobile
            })
            .then(res => {
              resultsCode = "";
              if (res.data.status == 0) {
                this.$message.error("");
                this.ruleForm.mobile = "";
              } else {
                this.getsms();
              }
            });
        }
      } else {
        this.$message.error("");
      }
    },
    //
    getsms() {
      const _this=this;
      this.$http
        .post("sms/", {
          mobile: this.ruleForm.mobile
        })
        .then(res => {
          resultsCode = res.data.results.code;
          let num = 60;
          this.timer = setInterval(() => {
            num > 0 ? num-- : clearInterval(_this.timer);
            _this.num = num;
          }, 1000);
          console.log("resultsCode111:", resultsCode);
          console.log("sms:", res);
        });
    },
    //
    getuserlist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
    },
    //
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getuserlist();
    },
    //
    handleSizeChange(val) {
      this.pageSize = val;
      this.getuserlist();
    },
    //
    openDialog(val) {
      this.disval = val;
      if (val == 1) {
        this.AddDialogVisible = true;
      }
    },
    //
    handleedit(obj) {
      this.getidinfo(obj.id);
      this.openDialog(2);
      this.actuserobj = obj;
    },
    //
    handledeploy(obj) {
      this.openDialog(3);
      this.actuserobj = obj;
    },

    //ID
    getidinfo(id) {
      this.$http.get("user/" + id).then(res => {
        let obj = { ...res.data };
        obj.remark = obj.remark ? obj.remark : "";
        obj.areaId = obj.areaId ? String(obj.areaId) : "";
        for (let key in obj) {
          this.ruleForm[key] = obj[key];
        }
      });
    },
    //
    changestatus(obj) {
      let _Url = "user/status",
        _parms = {
          status: "",
          id: obj.id
        };
      if (obj.status) {
        _parms.status = 1;
      } else {
        _parms.status = 0;
      }
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("");
        }
      });
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
      }
    },
    //
    savedata() {
      let _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      this.$http.post("register/", _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("");
          this.AddDialogVisible = !this.AddDialogVisible;
          this.getuserlist();
        }
      });
    }
  },
  created() {
    // this.getuserlist();
  }
};
</script>
<style lang="less">
.user_list {
  .topleft {
    .el-input {
      width: 40%;
    }
  }
  .diaul {
    width: 70%;
    padding-left: 15%;
    li {
      margin: 5px;
    }
    button {
      margin: 20px auto;
    }
  }
}
</style>



