
<template>
  <div class="jump mainboss">
    <div class="initform">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
        label-width="130px"
      >
        <el-form-item label="" clearable prop="zhuji">
          <el-select v-model="ruleForm.zhuji" clearable placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
 
        <el-form-item label="" prop="beifen">
          <el-select v-model="ruleForm.beifen" clearable placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input placeholder="" v-model="ruleForm.desc" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  name: "jump",
  data() {
    return {
      ruleForm: {
        zhuji: "",
        beifen: "",
        desc: ""
      },
      rules: {

        zhuji: [{ required: true, message: "", trigger: "change" }],
        beifen: [
          { required: true, message: "", trigger: "change" }
        ],
        desc: [{ required: true, message: "", trigger: "blur" }],

      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("ruleForm:", this.ruleForm);
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>



<style lang="less" scoped>
.initform {
  //   width: 450px;
  form {
    text-align: left;
    .el-form-item__label {
      text-align: left;
    }
    .el-input{
        width: 220px;
    }
  }
}
</style>


