<template>
  <div class="backups mainboss">
    <div class="initform">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
        label-width="130px"
      >
        <el-form-item label="" prop="date1">
          <el-select v-model="ruleForm.date1" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="leixin">
          <el-select v-model="ruleForm.leixin" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="ruleForm.type">
            <el-checkbox label="" name="type0"></el-checkbox>
            <el-checkbox label="" name="type1"></el-checkbox>
            <el-checkbox label="" name="type2"></el-checkbox>
            <el-checkbox label="" name="type3"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="beifen">
          <el-select v-model="ruleForm.beifen" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  name: "ass_init",
  data() {
    return {
      ruleForm: {
        date1: "",
        date2: "",
        leixin: "",
        beifen: "",
        type: [],
        desc: ""
      },
      rules: {
        date1: [{ required: true, message: "", trigger: "change" }],
        leixin: [
          { required: true, message: "", trigger: "change" }
        ],
        beifen: [
          { required: true, message: "", trigger: "change" }
        ],
        desc: [{ required: true, message: "", trigger: "blur" }],
        type: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("ruleForm:", this.ruleForm);
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>



<style lang="less" scoped>
.initform {
  form {
    text-align: left;
  }
}
</style>


